import React, {Component} from 'react'
import {actions} from "mirrorx";
import { FormControl, Select, InputNumber} from "tinper-bee";
import FormList from 'components/FormList';
import DatePicker from "tinper-bee/lib/Datepicker";
import {RefIuapDept} from 'components/RefViews';
import SearchPanel from 'components/SearchPanel';
import SelectMonth from 'components/SelectMonth';

import {deepClone, mergeListObj, delListObj} from "utils";
import zhCN from "rc-calendar/lib/locale/zh_CN";

import './index.less'

const FormItem = FormList.Item;
const {Option} = Select;
const format = "YYYY";
const {YearPicker} = DatePicker;


class SearchAreaForm extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    /** 
     * @param {*} error 
     * @param {*} values 
     */
    search = () => {
        this.props.form.validateFields((err, values) => {
            // 
            if (values.year) {
                values.year = values.year.format('YYYY');
            }
            // 
            const {dept} = values;
            if (dept) {
                const {refpk} = JSON.parse(dept);
                values.dept = refpk;
            }

            let queryParam = deepClone(this.props.queryParam);
            let {pageParams} = queryParam;
            pageParams.pageIndex = 0;

            const arrayNew = this.getSearchPanel(values); //
            // queryParam.whereParams = mergeListObj(whereParams, arrayNew, "key"); //

            queryParam.whereParams=arrayNew;

            actions.query.updateState({cacheFilter: arrayNew});  //
            actions.query.loadList(queryParam);
            this.props.clearRowFilter()
        });


    }


    /**
     *  
     */
    reset = () => {
        this.props.form.resetFields();
        this.props.form.validateFields((err, values) => {
            let queryParam = deepClone(this.props.queryParam);
            let {whereParams} = queryParam;

            const arrayNew = [];
            for (const field in values) {
                arrayNew.push({key: field});
            }
            queryParam.whereParams = delListObj(whereParams, arrayNew, "key"); //
            actions.query.updateState({queryParam});  //
        });
    }


    /**
     *
     * @param values search 
     * @returns {Array}
     */

    getSearchPanel = (values) => {
        const list = [];
        for (let key in values) {

            if (values[key] || ((typeof values[key]) === "number")) {
                let condition = "LIKE";
                // 
                const equalArray = ["code", "month"]; // 
                const greaterThanArray = ["serviceYearsCompany"]; //  
                if (equalArray.includes(key)) { //  
                    condition = "EQ";
                }
                if (greaterThanArray.includes(key)) { //  
                    condition = "GTEQ";
                }
                list.push({key, value: values[key], condition}); //
            }
        }
        return list;

    }


    render() {
        const _this = this;
        const {form,onCallback} = _this.props;
        const {getFieldProps} = form;
        return (
            <SearchPanel
                reset={this.reset}
                onCallback={onCallback}
                search={this.search}
            >
                <FormList size="sm">
                    <FormItem
                        label=""
                    >
                        <FormControl placeholder='' {...getFieldProps('code', {initialValue: ''})}/>
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <FormControl placeholder='' {...getFieldProps('name', {initialValue: ''})}/>
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <RefIuapDept {...getFieldProps('dept', {initialValue: ''})}/>
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <InputNumber
                            min={0}
                            iconStyle="one"
                            {...getFieldProps('serviceYearsCompany', {initialValue: "",})}
                        />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <YearPicker
                            {...getFieldProps('year', {initialValue: null})}
                            format={format}
                            locale={zhCN}
                            placeholder=""
                        />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <SelectMonth {...getFieldProps('month', {initialValue: ''})} />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <Select {...getFieldProps('exdeeds', {initialValue: ''})}>
                            <Option value=""></Option>
                            <Option value="0"></Option>
                            <Option value="1"></Option>
                        </Select>
                    </FormItem>
                </FormList>


            </SearchPanel>
        )
    }
}

export default FormList.createForm()(SearchAreaForm)
