import React, {Component} from 'react';
import {actions} from 'mirrorx';
import {Tooltip, Menu, Icon, Loading} from 'tinper-bee';
import queryString from "query-string";
import moment from 'moment'
import Grid from 'components/Grid';
import Header from 'components/Header';
import Button from 'components/Button';
import PopDialog from 'components/Pop';
import SearchArea from '../SearchArea'
import Dropdown from 'bee-dropdown'
import {deepClone, getHeight, getSortMap} from "utils";

import './index.less';

const {Item} = Menu;
const format = "YYYY-MM-DD HH:mm:ss";
const beginFormat = "YYYY-MM-DD 00:00:00";
const endFormat = "YYYY-MM-DD 23:59:59";


class IndexView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tableHeight: 0,
            filterable: false,
            showModal: false,
            record: {}, // 
        }

    }

    componentWillMount() {
        //
        this.resetTableHeight(true);
    }
    componentDidMount() {
        actions.query.loadList(this.props.queryParam); // 
    }

    componentWillReceiveProps(nextProps) {
        //  colFilterSelectdept 
        if (!this.props.colFilterSelectdept && nextProps.colFilterSelectdept) {
            for (let i = 0, len = this.gridColumn.length; i<len; i++) {
                const item = this.gridColumn[i]
                if (item.key === 'dept') {
                    item.filterDropdownData = nextProps.colFilterSelectdept;
                    break;
                }
            }
        }
    }

    /**
     *
     * 
     * @param {object} record 
     * @param {string} key menukey
     */
    onRelevance = (record, key) => {
        const {name} = record;
        if (key === "name") { // 
            window.open('https://baike.baidu.com/item/' + name, "_blank");
        }
        if (key === "code") {  // 
            this.setState({record, showModal: true});
        }
        if (key === "year") {  // 
            const {code, name, sexEnumValue, levelName} = record;
            actions.routing.push({
                pathname: '/employee',
                search: queryString.stringify({code, name, sexEnumValue, levelName})
            })
        }
    }

    /**
     *
     *
     * @param {Array} sortParam 
     */
    sortFun = (sortParam) => {
        let {queryParam} = this.props;
        queryParam.sortMap = getSortMap(sortParam);
        actions.query.loadList(queryParam);
    }

    /**
     *
     *
     * @param {string} key 
     * @param {*} value 
     * @param {string} condition 
     */
    onFilterChange = (key, value, condition) => {
        let isAdd = true; //
        let queryParam = deepClone(this.props.queryParam);
        let {whereParams, pageParams} = queryParam;
        pageParams.pageIndex = 0; // 
        for (const [index, element] of whereParams.entries()) {
            if (element.key === key) { // action  
                whereParams[index] = this.handleFilterData(key, value, condition);
                isAdd = false;
            }
        }
        if (isAdd) {
            const filterData = this.handleFilterData(key, value, condition);
            whereParams.push(filterData);
        }
        actions.query.loadList(queryParam);
    }


    /**
     *
     * 
     * @param {string} key 
     * @param {*} value 
     * @param {string} condition 
     */

    handleFilterData = (key, value, condition) => {
        const filterObj = {key, value, condition};
        if (Array.isArray(value)) { // 
            filterObj.value = this.handleDateFormat(value); // moment 
            filterObj.condition="RANGE";
        }
        return filterObj;
    }


    /**
     * 
     * @param {string} key 
     */
    onFilterClear = (key) => {
        let queryParam = deepClone(this.props.queryParam);
        let {whereParams, pageParams} = queryParam;
        for (const [index, element] of whereParams.entries()) {
            if (element.key === key) {
                whereParams.splice(index, 1);
                pageParams.pageIndex = 0; // 
                break;
            }
        }
        actions.query.loadList(queryParam);
    }


    /**
     *
     *
     * @param {Array} value 
     * @returns
     */
    handleDateFormat = (value) => {
        let dateArray = value.map((item, index) => {
            let str = '';
            if (index === 0) {
                str = item.format(beginFormat);
            } else {
                str = item.format(endFormat);
            }
            return str;
        });
        return dateArray;
    }

    /**
     *
     * @param {Number} pageIndex 
     */
    freshData = (pageIndex) => {
        this.onPageSelect(pageIndex, 0);
    }

    /**
     *
     * @param {Number} index 
     * @param {Number} value 
     */
    onDataNumSelect = (index, value) => {
        this.onPageSelect(value, 1);
    }

    /**
     *
     * @param {Number} value  pageIndex  pageIndex
     * @param {Number} type 0 pageIndex,1 pageSize
     */
    onPageSelect = (value, type) => {
        let queryParam = deepClone(this.props.queryParam); //  action 
        let {pageIndex, pageSize} = queryParam.pageParams;
        if (type === 0) {
            pageIndex = value - 1;
        } else {
            pageSize = value;
            pageIndex = 0;
        }
        if (value && value.toString().toLowerCase() === "all") { //  pageSize  all 
            pageSize = 1;
        }
        queryParam['pageParams'] = {pageIndex, pageSize};
        actions.query.loadList(queryParam);
    }

    /**
     *
     * @param {Boolean} status /
     */
    afterRowFilter = (status) => {
        if (!status) { // 
            let {queryParam, cacheFilter} = deepClone(this.props);
            queryParam.whereParams = cacheFilter;
            actions.query.updateState({queryParam}); //
        }
        this.setState({filterable: status});
    }


    clearRowFilter = () => {
        this.setState({filterable: false});
    }

    /**
     * 
     */
    close = () => {
        this.setState({showModal: false});
    }

    /**
     * excel
     */
    export = () => {
        this.grid.exportExcel();
    }
    /**
     * 
     *
     * @param {Boolean} isopen 
     */
    resetTableHeight = (isopen) => {
        let tableHeight = 0;
        if (isopen) {
            //px
            tableHeight = getHeight() - 470
        } else {
            //px
            tableHeight = getHeight() - 270
        }
        this.setState({ tableHeight });
    }

    gridColumn = [
        {
            title: "",
            width: 80,
            dataIndex: "k",
            key: "k",
            fixed: "left",
            className: 'data-cls ',
            exportHidden: true, //
            render: (text, record, index) => {

                //
                const menu = (
                    <Menu
                        onClick={e => this.onRelevance(record, e.key)}>
                        <Item key='code'></Item>
                        <Item key='year'></Item>
                        <Item key='name'></Item>
                    </Menu>
                );
                return (
                    <div className="table-menu">
                        <Dropdown
                            trigger={['click']}
                            overlay={menu}
                            animation="slide-up"
                        >
                            <Icon type="uf-link" style={{"color": "#004898"}}/>
                        </Dropdown>
                    </div>
                )
            }
        },
        {
            title: "",
            dataIndex: "code",
            key: "code",
            width: 160,
        },
        {
            title: "",
            dataIndex: "name",
            key: "name",
            width: 120,
            filterType: "text",
            filterDropdownType: "string",
            filterDropdown: "show",
            sorter: true, //true
            render: (text, record, index) => {
                return (
                    <Tooltip inverse overlay={text}>
                        <span>{text}</span>
                    </Tooltip>
                );
            }
        },
        {
            title: "",
            dataIndex: "sex",
            key: "sex",
            exportKey: 'sexEnumValue',
            width: 120,
            filterType: "dropdown",
            filterDropdown: "hide", //stringnumber
            filterDropdownAuto: "manual", // filterDropdownData 
            filterDropdownData: [{key: "", value: "1"}, {key: "", value: "0"}],
            render: (text, record, index) => {
                return (<span>{record.sexEnumValue}</span>)
            }

        },
        {
            title: "",
            dataIndex: "dept",
            key: "dept",
            exportKey: "deptName",
            width: 150,
            filterType: "dropdown",
            filterDropdown: "hide",
            filterDropdownAuto: "manual",
            filterDropdownData: this.props.colFilterSelectdept,
            filterDropdownFocus: () => { //
                if (!this.props.colFilterSelectdept) {
                    let param = {
                        distinctParams: ['dept']
                    }
                    actions.query.getListByCol(param); //
                }

            },
            render: (text, record, index) => {
                return (<span>{record.deptName}</span>)
            }
        },
        {
            title: "",
            dataIndex: "levelName",
            key: "levelName",
            width: 120,
        },
        {
            title: "",
            dataIndex: "serviceYears",
            key: "serviceYears",
            width: 180,
            // filterDropdown: "hide", //stringnumber
            filterType: "number",//
            filterDropdownType: "number",//
            className: 'column-number-right ', // 
            filterInputNumberOptions: {
                max: 100,
                min: 0,
                step: 1,
                precision: 0
            },
            sorter: true
        },
        {
            title: "",
            dataIndex: "serviceYearsCompany",
            key: "serviceYearsCompany",
            width: 130,
            className: 'column-number-right ', // 
            sorter: true
        },
        {
            title: "",
            dataIndex: "year",
            key: "year",
            width: 100,
            render: (text, record, index) => {
                return <div>{text ? moment(text).format("YYYY") : ""}</div>
            }
        },
        {
            title: "",
            dataIndex: "monthEnumValue",
            key: "monthEnumValue",
            width: 100,
            className: 'column-number-right ', // 
            sorter: true
        },
        {
            title: "",
            dataIndex: "allowanceTypeEnumValue",
            key: "allowanceTypeEnumValue",
            width: 120,
        },
        {
            title: "",
            dataIndex: "allowanceStandard",
            key: "allowanceStandard",
            width: 120,
            className: 'column-number-right ', // 
            render: (text, record, index) => {
                return (<span>{(typeof text)==='number'? text.toFixed(2):""}</span>)
            }

        },
        {
            title: "",
            dataIndex: "allowanceActual",
            key: "allowanceActual",
            width: 120,
            className: 'column-number-right ', // 
            render: (text, record, index) => {
                return (<span>{(typeof text)==='number'? text.toFixed(2):""}</span>)
            }

        },
        {
            title: "",
            dataIndex: "exdeedsEnumValue",
            key: "exdeedsEnumValue",
            width: 120,
        },
        {
            title: "",
            dataIndex: "applyTime",
            key: "applyTime",
            width: 300,
            filterDropdown: "hide", //stringnumber
            filterType: "daterange",//
            filterDropdownType: "daterange",//
            render: (text, record, index) => {
                return <div>{text ? moment(text).format(format) : ""}</div>

            }
        },
        {
            title: "",
            dataIndex: "pickTypeEnumValue",
            key: "pickTypeEnumValue",
            width: 120,
        },
        {
            title: "",
            dataIndex: "pickTime",
            key: "pickTime",
            width: 150,
            render: (text, record, index) => {
                return <div>{text ? moment(text).format(format) : ""}</div>
            }
        },
        {
            title: "",
            dataIndex: "remark",
            key: "remark",
            width: 100,
        }
    ]

    render() {
        const _this = this;
        const {queryObj, showLoading, queryParam} = _this.props;
        const {pageIndex, total, totalPages} = queryObj;
        const {filterable, record, tableHeight} = _this.state;

        const paginationObj = {   // 
            activePage: pageIndex,//
            total,//
            items: totalPages,
            freshData: _this.freshData,
            onDataNumSelect: _this.onDataNumSelect,
        }

        // const
        const sortObj = {  //
            mode: 'multiple',
            backSource: true,
            sortFun: _this.sortFun
        }

        return (
            <div className='single-table-query'>
                <Loading showBackDrop={true} show={showLoading} fullScreen={true}/>
                <Header title='A1'/>
                <SearchArea
                    queryParam={queryParam}
                    clearRowFilter={this.clearRowFilter}
                    onCallback={this.resetTableHeight}
                />
                <div className='table-header'>
                    <Button
                        className="ml8"
                        colors="primary"
                        onClick={_this.export}
                    ></Button>
                </div>
                <div className="gird-parent">
                    <Grid
                        ref={(el) => this.grid = el} //
                        columns={this.gridColumn}
                        data={queryObj.list}
                        rowKey={(r, i) => i} //key
                        paginationObj={paginationObj} //
                        multiSelect={false}  //false 
                        showFilterMenu={true} //
                        filterable={filterable}//
                        onFilterChange={_this.onFilterChange}  // 
                        onFilterClear={_this.onFilterClear} //
                        afterRowFilter={_this.afterRowFilter} ///
                        sort={sortObj} //
                        scroll={{y: tableHeight}}
                        sheetHeader={{height: 30, ifshow: false}} //excelheightifshow
                    />
                </div>

                <PopDialog
                    show={this.state.showModal}
                    title={""}
                    close={this.close}
                    btns={[]}
                >
                    <div>
                        <span></span>
                        <span>{record.code}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{record.name}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{record.sexEnumValue}</span>
                    </div>
                    <div>
                        <span></span>
                        <span>{record.levelName}</span>
                    </div>
                </PopDialog>
            </div>
        )
    }
}

export default IndexView;
